body {
  margin: 0;
  padding: 0;
  font-family: 'Bree Serif', serif;
}

h1, h2, h3, h4, h5, p, div {
  font-family: 'Bree Serif', serif;
}

.fade {
  position: relative;
  height: 3.7em; /* exactly three lines */
}

.fade:after {
  content: "";
  text-align: right;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 70%;
  height: 1em;
  background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(50%, rgba(255, 255, 255, 1)));
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 50%);
  background: -o-linear-gradient(left, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 50%);
  background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 50%);
}

.module {
  width: 100%;
  overflow: hidden;
}



